nav {
	z-index: 2;
}
.parallax-provider {
	top:-50px;
}
.logo {
	margin-top: 3px;
	position: absolute;
	width: 177.58px;
	height: 35.58px;
	left: 130px;
}
.logo:hover {
	cursor: pointer;
	transform: scale(1.01);
	filter: invert(53%) sepia(100%) saturate(3103%) hue-rotate(166deg) brightness(100%) contrast(86%);
}
.navigation-button{
	position: absolute;
	width: 111px;
	height: 40px;
	left: 1199px;
	background: #2D9CDB;
	border-radius: 3px;
	border: none;
	cursor: pointer;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	color: #FFFFFF;
}.navigation-button:hover{
    background: #2D9CDB;
}
.navigation-button:active{
    box-shadow:inset 0 0 5px  #005987;
}
.navigation-link{
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	color: #FFFFFF;
	margin-left: 19px;
	margin-right: 19px;
}.navigation-link:hover{
	color: #12A3DD;
	text-decoration: none;
}