	top: 30px;
	width: 150px;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 13pt;
	text-transform: uppercase;
	word-wrap: break-word;
	color: #FFFFFF;
}

